.plases {
  padding-top: 51px;
  max-width: 880px;
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 282px);
  row-gap: 20px;
  column-gap: 17px;
  margin: 0 auto;
}

@media (max-width: 880px) {
  .plases {
    padding-top: 36px;
    max-width: 280px;
  }
}